import Vue from "vue";
import VueRouter from "vue-router";
import RegistrationPage from "@/pages/RegistrationPage.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "registration",
    component: RegistrationPage,
  },
  {
    path: "/successful",
    name: "successfulMessage",
    component: () => import("../pages/SuccessfulMessagePage.vue"),
  },
  {
    path: "/error",
    name: "errorMessage",
    component: () => import("../pages/ErrorMessagePage.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
