<template>
  <label :class="classes.label">
    <button @click.prevent="typeChange" :class="classes.showPass" tabindex="-1">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="21"
        height="10"
        viewBox="0 0 21 10"
        fill="none"
      >
        <path
          d="M0.16272 4.29373C0.661755 3.78874 1.18472 3.32952 1.72648 2.91608C4.17722 1.04917 7.08598 0.0520564 10.0853 0.00198599C13.0778 -0.0480844 16.1472 0.84603 18.921 2.76158C19.5841 3.21937 20.225 3.73008 20.8419 4.29803C21.0402 4.48114 21.0504 4.75152 20.8812 4.94322C20.1754 5.77725 19.3636 6.49111 18.4766 7.08337C16.2566 8.56688 13.5598 9.29648 10.827 9.30793C8.10456 9.31794 5.3479 8.61266 2.9997 7.22357C1.95036 6.6027 0.981343 5.84592 0.131957 4.95609C-0.0577443 4.75581 -0.038945 4.47399 0.16272 4.29373ZM10.5006 1.54845C11.5243 1.54845 12.4523 1.89608 13.124 2.4583C13.7956 3.02052 14.2109 3.79589 14.2109 4.65424C14.2109 5.51116 13.7956 6.28797 13.124 6.85019C12.4523 7.4124 11.526 7.76004 10.5006 7.76004C9.4769 7.76004 8.5489 7.4124 7.87726 6.85019C7.20561 6.28797 6.79032 5.51259 6.79032 4.65424C6.79032 3.79732 7.20561 3.02052 7.87726 2.4583C8.5489 1.89608 9.4769 1.54845 10.5006 1.54845ZM12.2865 3.15928C11.8302 2.77732 11.1979 2.53984 10.5006 2.53984C9.80333 2.53984 9.17099 2.77589 8.71468 3.15928C8.25837 3.54125 7.97467 4.07056 7.97467 4.65424C7.97467 5.23792 8.25666 5.76723 8.71468 6.1492C9.17099 6.53117 9.80333 6.76864 10.5006 6.76864C11.1979 6.76864 11.8302 6.5326 12.2865 6.1492C12.7429 5.76723 13.0265 5.23792 13.0265 4.65424C13.0265 4.07056 12.7429 3.54125 12.2865 3.15928ZM2.5451 3.66714C2.14861 3.96899 1.76578 4.29517 1.40005 4.6428C2.09563 5.31803 2.86981 5.90171 3.69869 6.3924C5.84181 7.65846 8.34895 8.30223 10.8236 8.29364C13.288 8.28363 15.72 7.62556 17.7229 6.28797C18.4066 5.83161 19.0389 5.29514 19.608 4.67856C19.1312 4.26226 18.6458 3.88602 18.1536 3.54554C15.6089 1.79021 12.818 0.971921 10.1092 1.01627C7.40728 1.06205 4.77367 1.96904 2.5451 3.66714Z"
          fill="#A4A4A6"
        />
      </svg>
    </button>
    <input
      v-model="inputValue"
      ref="input"
      :class="classes.input"
      :placeholder="placeholder"
      :type="currentType"
    />
    <span v-if="errorMessage" :class="classes.error">
      {{ errorMessage }}
    </span>
  </label>
</template>

<script>
export default {
  name: "BaseInputPass",
  props: {
    value: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
    placeholder: String,
    errorMessage: {
      type: String,
      default: "",
    },
    classes: {
      type: Object,
      default: () => ({}),
      require: false,
    },
  },
  emits: ["update:value"],
  data() {
    return {
      currentType: "password",
    };
  },
  computed: {
    inputValue: {
      get() {
        return this.value;
      },
      set(newValue) {
        this.$emit("input", newValue);
      },
    },
  },
  methods: {
    typeChange() {
      this.currentType = this.currentType === "password" ? "text" : "password";
    },
  },
};
</script>
